body {
  font-weight: 100;
}

h1,
h2,
h3,
h4 {
  font-weight: 100;
  margin: 20px 0;
}

section {
  margin: 50px 0;
}

.coaster-card img {
  width: 100%;
  height: 150px;
  object-fit: cover;
}

.coaster-card:hover {
  box-shadow: 0 0 2px 1px rgba(0, 5, 7, 0.281);
}

.coaster-card {
  padding: 0 20px;
  margin: 20px 0;
}

.coaster-card h3 {
  font-size: 1.2em;
  margin: 5px 0;
}

.coaster-card p {
  font-size: 0.8em;
  margin-bottom: 0;
  height: 150px;
  overflow: scroll;
}

.coaster-card small {
  font-size: 0.7em;
}

.coaster-details img {
  width: 100%;
}

.navbar-nav a {
  color: inherit;
}

.pointer {
  cursor: pointer;
}

.pointer:hover {
  text-decoration: underline;
}

button {
  width: 100px;
}

.btn {
  width: 100px;
}

.btnFlex {
  display: flex;
  justify-content: space-around;
}

.fpimg {
  border: 1px solid #ddd;
  border-radius: 4px;
  padding: 5px;
  width: 100%;
}

.fpimg:hover {
  box-shadow: 0 0 2px 1px rgba(0, 5, 7, 0.5);
}

footer {
  position: absolute;
  bottom: 0;
  width: 100%;
}
